import React, { Component } from 'react';

export class Banner extends Component {
	render() {
		return (
  <section className='banner banner-wrapper'>
    <a id='logo'>
      <img src='assets/images/logo.png' alt='logo' />
    </a>
  </section>
		);
	}
}



